.nav {
  width: 19.76vw;
  height: 100vh;

  background-color: #23262b;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  padding-left: 3vw;

  color: #828ea0;

  font-family: Gotham Pro;
  font-size: 14pt;

  z-index: 16;
  position: fixed;
}
.nav > * {
  height: 8vh;
  cursor: pointer;
}
.logotype-client_agr {
  position: fixed;

  left: 3vw;
  top: 3vh;

  width: 7.6vw;

  z-index: 40;
}
.main-text-description {
  width: 59.8vw;
  position: absolute;
  right: 13vw;
  top: 10vh;
  margin-bottom: 10vh;
}

.title-name {
  font-size: 50pt;
  color: #828ea0;
  font-family: Gotham Pro;
  font-weight: bold;
}
.descr {
  font-size: 12pt;
  color: #828ea0;
  font-family: Gotham Pro;
  opacity: 0.5;
}

.positions_1 {
  font-size: 16pt;
  color: #828ea0;
  font-family: Gotham Pro;

  margin: 5vh 0 2vh 0;
}
