@media screen and (max-width: 400px) {
  .nav {
    width: 100vw;
    height: 100vh;
    padding-left: 20vw;
    z-index: 1;
  }
  .logotype-client_agr {display: none;}
  .UA__header > .header-title {background-color: inherit;}
  .title-name {
    font-size: 20pt; 
    margin-bottom: 4vh;
  }
  .main-text-description {width: 70vw;}
  .main-text-description>div>.descr{font-size: 8pt;}
  .main-text-description>div>.positions_1{font-size: 12pt;}


  .before_click{
    width: 50px;
    height: 70px;
    border-radius: 50%;
    background-color: #FFF700;
    transform: translate(-50%, 45vh);
  }
  .before_click>*{
    display: none;
  }
  .menu-close{
    position: absolute;
    width: 60px;
    top: 48.2vh;
    left: 10px;
    z-index: 1200;
  }
  .open>svg>rect{
    stroke: #FFF700;
    fill: #FFF700;
  }

}
